//import Header from './Header';
//import routes from '../root.config';
//import {Button,TabBar} from 'antd-mobile';
import React from 'react';
import {TabBar} from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {

	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'blueTab',
	  };
	}

	render() {

		const {pathname} =this.props.location;

	  return (
		<div 
			style={{ 
				position: 'fixed',
				width: '100%', 
				bottom: 0 
			}}
		>
			
		  	<TabBar
				unselectedTintColor="#949494"
				tintColor="red"
				barTintColor="white"
		  	>
				<TabBar.Item
					title="首页"
					key="home"

					icon={
						<i className='iconfont icon-shouye'></i>
					}
					selectedIcon={
						<i className='iconfont icon-shouye'></i>
					}
					
					selected={pathname==='/'}
					onPress={() => {
						this.props.history.push('/');
					}}
					data-seed="logId"
			></TabBar.Item>

			<TabBar.Item
				title="收藏"
				key="collect"

			  	icon={
					<i className='iconfont icon-shoucang'></i>
				}
				selectedIcon={
					<i className='iconfont icon-shoucang'></i>
				}

				selected={pathname==='/collect'}
				onPress={() => {
					this.props.history.push('/collect');
				}}
				data-seed="logId1"
			></TabBar.Item>

			<TabBar.Item
				title="购物车"
				key="cart"

			  	icon={
					<i className='iconfont icon-gouwuche1'></i>
				  }
				  
			  	selectedIcon={
					<i className='iconfont icon-gouwuche1'></i>
				  }

			  	selected={pathname==='/cart'}
			  	onPress={() => {
					this.props.history.push('/cart');
				}}  
			  	data-seed="logId1"
			></TabBar.Item>

			{/* <TabBar.Item
				title="订单列表"
				key="friend"

			  	icon={
					<i className='iconfont icon-dingdanliebiao'></i>
				}
				selectedIcon={
					<i className='iconfont icon-dingdanliebiao'></i>
				}

				selected={pathname==='/friend'}
				onPress={() => {
					this.props.history.push('/friend');
				}}
				data-seed="logId1"
			></TabBar.Item> */}

			<TabBar.Item
				title="我的淘宝"
				key="my"

				icon={
					<i className='iconfont icon-wo'></i>			
				}
				selectedIcon={
					<i className='iconfont icon-wo'></i>
				}

				selected={pathname==='/my'}
				onPress={() => {
					this.props.history.push('/my');
				}}
			></TabBar.Item>

			{/* <TabBar.Item
				title="更多"
				key="more"

				icon={
					<i className='iconfont icon-gengduo'></i>
				}
				selectedIcon={
					<i className='iconfont icon-gengduo'></i>
				}

				selected={pathname==='/more'}
				onPress={() => {
					this.props.history.push('/more');
				}}
			></TabBar.Item> */}

		  </TabBar>
		</div>
	  );//return
	}//render
}//class

export default withRouter(Mytab);
