import React from 'react'
import {connect} from 'react-redux'
import {withRouter} from 'react-router-dom'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch ,Link} from 'react-router-dom'
import './login.css'
import {signIN} from './actionCreators'
import Collect from './collect'

const Login = (props) => {
    console.log(props)
    const loginClick=()=>{
        props.dispatch({
            type:'LOGIN',
            value: '张三'
        })
        props.history.push('/shoucang')
        console.log(props)
    }

    

   
    return (
        <div className="ddiv">
            {/* header */}
            <div className="loginHead">
                您需要登陆才能继续访问
                <Link to="/">
                    <a className="close">关闭</a>
                </Link>
            </div>

            {/* logo */}
            <div className="logo">
                <div className="tb-logo"></div>
            </div>

            <form className="loginForm">
                <div className="id-div">
                    <input name="login-id" 
                        type="text" 
                        className="loginId" 
                        tabIndex="1" 
                        aria-label="手机号/邮箱/会员名" 
                        placeholder="手机号/邮箱/会员名"
                        autoCapitalize="off" 
                    />
                </div>

                <div className="pwd-div">
                    <input name="login-password" 
                        type="password" 
                        className="loginPwd" 
                        tabIndex="2" 
                        aria-label="请输入登录密码" 
                        placeholder="请输入登录密码" 
                        maxLength="40" 
                        autoCapitalize="off" 
                    />
                </div>

                <div className="yanzheng-div">
                    <a href="#" className="codeLogin">短信验证码登录</a>
                    <a href="https://reg.taobao.com/member/new_register.jhtml?TPL_redirect_url=https%3A%2F%2Fh5.m.taobao.com%2Fother%2Floginend.html%3Forigin%3Dhttps%253A%252F%252Fmain.m.taobao.com" 
                        target="_top" 
                        className="linkLogin"
                    >免费注册</a>
                </div>

                <div>
                <button type="submit" tabIndex="3" className="loginBtn" 
                onClick={loginClick}>登录</button>
                </div>
            </form>
        </div>   
    )
}


const mapStateToProps=(state)=>{
    console.log(state)
    return state.loginReducer.userinfo
}
  
export default connect(mapStateToProps)(withRouter(Login))
  