import { SearchBar } from 'antd-mobile';
import React from 'react'
import Search from './search'
import Mytab from './Mytab';
import Slideshows from './slideshows'
import Listt from './Listt';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch ,Link} from 'react-router-dom'

const Home=()=>{
    return (
        <div>
            <Link to='/searchbaarr'>  
               <div>
                   <Search />
               </div> 
            </Link>

            <Slideshows />

            <div 
                className="sub-title"
                style={{
                    color:'darkorange',
                    fontSize:16,
                    textAlign:'center',
                    margin:20
                }}
            > 
                --你可能还喜欢-- 
            </div>

            <Listt />

            <Mytab />

        </div>
    )
}

export default Home;