import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';
class slideshows extends React.Component {
  state = {
    data: [
        'https://gw.alicdn.com/imgextra/i1/77/O1CN01FDrchK1CRJ7zNeOCd_!!77-0-lubanu.jpg',
        'https://gw.alicdn.com/imgextra/i2/133/O1CN01A4gGiv1CqxJ49nQfB_!!133-0-lubanu.jpg',
        'https://aecpm.alicdn.com/simba/img/TB13jCmflFR4u4jSZFPSuunzFXa.jpg_q50.jpg',
        'https://gw.alicdn.com/imgextra/i2/154/O1CN01LiwSpf1D0ZcpKwv6T_!!154-0-lubanu.jpg',
        'https://img.alicdn.com/simba/img/TB1R6YXErr1gK0jSZR0SuvP8XXa.jpg_q50.jpg',
        'https://aecpm.alicdn.com/imgextra/i1/353930884/O1CN01oXkFbI1IOujtiv5he_!!353930884-0-alimamacc.jpg_q50.jpg'
    ],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: [
            'https://gw.alicdn.com/imgextra/i1/77/O1CN01FDrchK1CRJ7zNeOCd_!!77-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i2/133/O1CN01A4gGiv1CqxJ49nQfB_!!133-0-lubanu.jpg',
            'https://aecpm.alicdn.com/simba/img/TB13jCmflFR4u4jSZFPSuunzFXa.jpg_q50.jpg',
            'https://gw.alicdn.com/imgextra/i2/154/O1CN01LiwSpf1D0ZcpKwv6T_!!154-0-lubanu.jpg',
            'https://img.alicdn.com/simba/img/TB1R6YXErr1gK0jSZR0SuvP8XXa.jpg_q50.jpg',
            'https://aecpm.alicdn.com/imgextra/i1/353930884/O1CN01oXkFbI1IOujtiv5he_!!353930884-0-alimamacc.jpg_q50.jpg'
        ],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank style={{margin:0}}>

        <Carousel
            autoplay
            autoplayInterval={3000}
            infinite
            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
            afterChange={index => console.log('slide to', index)}
            style={{width:'100%'}}
        >
            {this.state.data.map(val => (
                <a
                    key={val}
                    href="http://www.alipay.com"
                    style={{ 
                        display: 'inline-block', 
                        width: '100%', 
                        height: this.state.imgHeight 
                    }}
                >
                    <img
                        src={`${val}`}
                        alt=""
                        style={{ 
                            width: '100%', 
                            height:130,
                            verticalAlign: 'top' 
                        }}
                        onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                        }}
                />
                
                </a>
            ))}
        </Carousel>

      </WingBlank>
    );
  }
}

export default slideshows;