import React from 'react'
import Bottom from './Bottom'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch ,Link} from 'react-router-dom'
import './detail.css'


const detail = (props) => {
    let id=props.location.state.dataItem.id
    let img=props.location.state.dataItem.img
    let title=props.location.state.dataItem.title
    let price=props.location.state.dataItem.price

    return (
        <div>
            <Link to='/'>
                <button className="return"><i className='iconfont icon-icon-test'></i></button>
            </Link>
            
            <img
                src={img}
                alt=""
                style={{width:'100%'}}
            ></img>
            <p className="price">￥{price}</p>
            <p className="title">{title}</p>
            <Bottom img={img} title={title} price={price} id={id}/>
        </div>  
    )
}

export default detail