let collect=[]

const collectReducer = (state=collect,action)=>{
    console.log(action)
    console.log(state)
    switch(action.type){
        case 'COLLECT':
            let srr={
                id:action.value.id,
                img:action.value.img,
                title:action.value.title,
                price:action.value.price
            }
            return [...state,srr]
        case 'DEL':
            let arr = [...state];
            for(let i=0;i<arr.length;i++){
                if(arr[i].id==action.index){
                    arr.splice(i,1);
                }
            }
            return arr;
        default:
            return state;
    }
}

export default collectReducer