import React,{useState,Component} from 'react'
import './bottom.css'
import {connect} from 'react-redux'

const Bottom=(props)=>{

    const [icoStatus, setIcoStatus] = useState(true)

     const collection=(event, props)=>{
        setIcoStatus(!icoStatus)
        props.dispatch({
            type:'COLLECT',
            value:{
                id:props.id,
                img:props.img,
                title:props.title,
                price:props.price
            }
        })
     }

    return(
        <div className="btm">
            <div className="leftbtn">
                <i className='iconfont icon-dianpu'></i>
                <p style={{margin:0}}>店铺</p>
            </div>

            <div className="leftbtn">
                <i className='iconfont icon-kefu'></i>
                <p style={{margin:0}}>客服</p>
            </div>

            <div className="leftbtn" onClick={(e) => collection(e,props)} >
                <i className={icoStatus ? "iconfont icon-shoucang" : "iconfont icon-shoucang1"}></i>
                <p style={{margin:0}}>收藏</p>
            </div>

            <div className="cart">
                加入购物车
            </div>

            <div className="buy">
                立即购买
            </div>    

        </div>
    );
}

const mapStateToProps=(state)=>{
    return {
        collections:state.collectReducer
    };
}

export default connect(mapStateToProps)(Bottom)