import React from 'react'
import Mytab from './Mytab'
import {connect} from 'react-redux'
import './collect.css'
import { Grid} from 'antd-mobile'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch,Link } from 'react-router-dom'

const Collect = (props) => {
    let collections=[]

    setTimeout(()=>{
        collections=props.collections
    },5000)

    collections=props.collections


    const delCollect=(id)=>{
        console.log(id)
        props.dispatch({
            type:'DEL',
            index:id
        })
    }

    return (
        <div>
            <Grid 
                data={collections} 
                columnNum={1} 
                square={false} 
                className="not-square-grid"
                style={{borderRadius:15}}
        
                renderItem={ dataItem => (
                <div className="collectt" id={dataItem.id}>
                    <div className="link">
                        <Link to={{pathname:'/detail',state:{dataItem:dataItem}}} >
                            <img className="image" src={dataItem.img} alt="" />
                            <div className="title">{dataItem.title}</div>
                            <div className="price">{dataItem.price}</div>
                        </Link>
                    </div>
                    <button  className="btn" onClick={()=>delCollect(dataItem.id)}>delete</button>
                </div>  
                )}

            />

            <Mytab />
        </div>
        
        
    )
}

const mapStateToProps=(state)=>{
    console.log(state.collectReducer)
    return {
        collections:state.collectReducer
    };
}

export default connect(mapStateToProps)(Collect)