import React from 'react'
import Mytab from './Mytab'
import Login from './login'

const Cart = (props) => {
    const isLoggedIn = props.isLoggedIn;

    if (isLoggedIn) {
        return (
            <p>购物车</p>,
            <Mytab />
        );
    }

    return <Login />;
}

export default Cart