import React from 'react';
import {connect} from 'react-redux'
import { Grid, List } from 'antd-mobile';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch,Link } from 'react-router-dom'
import './list.css'
import {getlist} from './actionCreators'

const Listt = (props) => {
  
  props.dispatch(getlist())
  console.log(props.mydata)

  let arr=[]
  setTimeout(()=>{
    arr=props.mydata
    console.log('waiting')
  },5000)

  arr=props.mydata

    return (
      <div>
        <Grid 
            data={arr} 
            columnNum={2} 
            square={false} 
            className="not-square-grid"
            style={{borderRadius:15}}
    
            renderItem={ dataItem => (
              <div style={{margin:0,padding:0}}>
                <Link to={{pathname:'/detail',state:{dataItem:dataItem}}} >
                  <img src={dataItem.img} style={{ width: '100%', height: '100%' }} alt="" />
                  <div className="text">{dataItem.title}</div>
                  <div style={{textAlign:'left',marginTop:8}}>
                    <span className="pay1">￥</span>
                    <span className="pay2">{dataItem.price}</span>
                  </div>
                </Link>
              </div>  
            )}

        />
      </div>
    );
}

const mapStateToProps=(state)=>{
  console.log(state)
  return {
      mydata:state.listReducer[0]
  };
}

export default connect(mapStateToProps)(Listt)
