import React from 'react'

const Search=(props)=>{
    return(
        <div 
            style={{backgroundColor:'orange'}}>
            <img 
                src={require("../images/tao.png")}
                style={{width:40,height:40,color:"orange"}}
            />
            <div
            style={{
                color:'white',
                float:'right',
                backgroundColor:'darkorange',
                width:'60%',
                height:25,
                borderRadius:10,
                lineHeight:2,
                marginTop:7,
                marginRight:'14%',
                textAlign:'center'
            }}>
                寻找宝贝店铺
            </div>
        </div>
    )
}
export default Search