import { SearchBar, TabBar } from 'antd-mobile';
import React from 'react';
import Mytab from './Mytab';
import Home from './Home'
import Cart from './Cart'
import Friend from './Friend'
import Collect from './shoucang.js'
import My from './My'
import More from './More'
import searchbaarr from './searchbaarr'
import Detail from './detail'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Provider} from 'react-redux'
import store from './store'
import Shoucang from './shoucang'

const App=()=>{

    return (
        <Provider store={store}>
        <Router>
            <Switch>
                <Route exact path='/' component={Home} />
                <Route path='/searchbaarr' component={searchbaarr} />
                <Route path='/detail' component={Detail} />
                <Route path='/cart' component={Cart} />
                <Route path='/friend' component={Friend} />
                <Route path='/collect' component={Collect} />
                <Route path='/my' component={My} />
                <Route path='/more' component={More} />
                <Route path='/shoucang' component={Shoucang} />
                <Route render={()=><Redirect to="/" />} />
            </Switch>
        </Router>
        </Provider>
        
    )
}

export default App;